<template>
    <article class="channel">
        <div class="log" :class="logStatus(log, index)" v-for="(log, index) in channelLogs">
            <div class="log-username"><span v-if="prevUsername(log.username, index)">{{ log.username }}</span></div>
            <div class="log-content">
                <div class="log-info">
                    <div class="log-title"><span>{{ log.title }}</span></div>
                    <div v-if="log.timestamp" class="log-timestamp"><span>{{ getDate(log.timestamp) }}</span></div>
                </div>
                <div class="log-body"><pre>{{ log.body }}</pre></div>
            </div>
        </div>
    </article>
</template>

<script>
    export default {
        name: 'Channel',
        props: [
            'logs'
        ],
        computed: {
            channelLogs() {
                return this.logs[this.$route.params.name];
            }
        },
        methods: {
            prevUsername(username, index) {
                return index === 0 || (index > 0 && username !== this.channelLogs[index - 1].username)
            },
            getDate(timestamp) {
                let date = new Date(timestamp);
                return `${date.toDateString()} ${date.toLocaleTimeString()}`
            },
            logStatus(log, index) {
                return {
                    'log-default': !log.status || log.status === 'default',
                    'log-success': log.status === 'success',
                    'log-error': log.status === 'error',
                    'log-newuser': this.prevUsername(log.username, index)
                }
            }
        }
    }
</script>

