<template>
    <div id="app">
        <header class="header"></header>
        <Loader v-if="loading"></Loader>
        <Channels :channels="channels"/>
        <router-view :logs="logs"></router-view>
        <aside class="right"></aside>
    </div>
</template>

<script>
	/* eslint-disable */
	import Loader from './components/Loader.vue'
	import Channel from './components/Channel.vue'
	import Channels from './components/Channels.vue'
	import Vue from 'vue'

	const {ipcRenderer} = window.require('electron');

	export default {
		name: 'app',
		components: {
			Loader,
			Channel,
			Channels
		},
		data() {
			return {
				logs: {},
				channels: {},
				loading: true
			}
		},
		methods: {
			showNotification(title, body, callback) {
				let notification = new Notification(title, {body});
				notification.onclick = () => {
					callback ? callback(title, body) : ''
				}
			},
			log({channel, log}, callback) {
				console.log("new: ", {channel, log});
				this.showNotification(channel.name, log.body, callback);
			}
		},
		mounted: function () {
			let self = this;
			ipcRenderer.on('new', (e, {channel, log}) => {
				this.log({channel, log}, (title, body) => {
					this.$router.push({path: `/channel/${title}`})
				});
				!self.logs[channel.name] ? Vue.set(self.logs, channel.name, []) : '';
				self.logs[channel.name].push(log);
				Vue.set(self.channels, channel.name, channel);
			});
			ipcRenderer.on('get', (e, {channels, logs}) => {
				console.log('get: ', {channels, logs});
				self.channels = channels;
				Object.keys(logs).forEach(function (key) {
					Vue.set(self.logs, key, logs[key])
				});
				this.loading = false;
			});
			ipcRenderer.send('get', '');
		}
	}
</script>

